import "../styles.css";

const ImgBox = ({ item, index }) => {
  const like = (id, index) => {
    let elem = document.querySelector("#item-" + id);
    if (elem.classList.contains("liked")) {
      elem.style.color = "blue";
      elem.classList.remove("liked");
      if (localStorage.getItem("imagesArr") !== null) {
        let img = localStorage.getItem("imagesArr");
        let decodeArr = JSON.parse(img);
        decodeArr.forEach(function (item) {
          if (item.id === id) {
            console.log(id);
            decodeArr[index].likes = 0;
          }
        });
        let encodedArr = JSON.stringify(decodeArr);
        localStorage.setItem("imagesArr", encodedArr);
      }
    } else {
      elem.style.color = "red";
      elem.classList.add("liked");
      if (localStorage.getItem("imagesArr") !== null) {
        let img = localStorage.getItem("imagesArr");
        let decodeArr = JSON.parse(img);
        // decodeArr[id].likes = 0;
        decodeArr.forEach(function (item) {
          if (item.id === id) {
            console.log(id);
            console.log(decodeArr[index]);
            decodeArr[index].likes = 1;
          }
        });
        let encodedArr = JSON.stringify(decodeArr);
        localStorage.setItem("imagesArr", encodedArr);
      }
    }
  };
  return (
    <>
      <div style={{ width: "auto" }} className="col-md-4">
        <img
          src={item.download_url}
          alt={item.author}
          key={item.id}
          height={200}
          width={200}
          style={{ padding: "5px" }}
        />
        <span
          style={{
            display: "flex",
            width: "200px",
            margin: "-60px 18px 27px 10px"
          }}
        >
          <p style={{ padding: "4px", margin: "4px", cursor: "pointer" }}>
            <i
              className={
                item.likes === 1
                  ? "liked fa fa-heart item "
                  : " fa fa-heart item "
              }
              id={"item-" + item.id}
              key={item.id}
              onClick={() => like(item.id, index)}
            ></i>
          </p>
          <p style={{ padding: "4px", margin: "4px", cursor: "pointer" }}>
            <i className="fa fa-comment"></i>
          </p>
        </span>
      </div>
    </>
  );
};

export default ImgBox;
